class GWFractals extends Component {

    state = {
        achievementData: [],
        idString: "",
    }

    render(){
        const rows = [];

        this.props.data.forEach((data)=>{
            //console.log(data.id);
            rows.push(
                <GWFractalsRow data={data} key={data.id}/>
            );
        })
        return(
            <div>{rows}</div>
        );
    }
}

class JStest extends Component {
    state = {
        fractalData: []
    }
    componentDidMount(){
        axios.get(GW2DAILYURL)
        .then(res => {
            const fractalData = res.data.fractals;
            this.setState({fractalData});
            //console.log(fractalData);
        });
    }

    render(){
        return(
            <div>
                <h1>This is a test</h1>
                <GWFractals data={this.state.fractalData}/>
            </div>
        );
    }
}

export default JStest;



